/* Style for the grid container */
.gameView {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 columns of equal width */
    row-gap: 16px; /* Space between grid items */
    padding: 3vw; /* Padding around the grid */
    justify-content: center; /* Center the grid horizontally if it's smaller than the container */
    align-items: start; /* Align items to the top of the row */
}

/* Style for individual grid items */
.singleGame {
    display: flex;
    flex-direction: column; /* Arrange items in a column */
    justify-content: flex-start; /* Align items at the top */
    align-items: center; /* Center items horizontally */
    background-color: #f9f9f9; /* Light background for the item */
    padding: 3vw; /* Padding inside each grid cell */
    border: 1px solid #ddd; /* Light border */
    border-radius: 8px; /* Rounded corners */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3); /* Optional shadow */
    text-align: center; /* Center text inside the item */
    width: 20vw;
    height : auto;
}

/* Style for images in the grid */
.singleGame img {
    width: 100%; /* Make the image fill the width of the container */
    max-width: 200px; /* Optional max-width for larger images */
    height: auto; /* Maintain aspect ratio */
    border-radius: 4px; /* Optional rounded corners */
    margin-bottom: 8px; /* Space below the image */
}

/* Style for text inside the grid item */
.singleGame p {
    margin: 0.1vw 0; /* Space between text elements */
    font-size: 2vw; /* Font size for readability */
    color: #333; /* Text color */
}

@media (min-width : 390px) {
    .NewGameBTN {
        border-radius: 25px;
        border-color: transparent;
        color: white;
        background-color: red;
        padding: 5px 20px 5px 20px;
        font-size: 10px;
        margin-top: 1%;
        margin-left: 25px;
        font-weight: bold;
    }

    .NewGameBTN:hover {
        border-radius: 25px;
        border-color: transparent;
        color: rgb(104, 104, 104);
        background-color: rgb(64, 1, 1);
        padding: 5px 20px 5px 20px;
        font-size: 10px;
        margin-top: 1%;
        margin-left: 25px;
        font-weight: bold;
    }
}

